.container {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    text-align: center;
}

/* 헤더 */
.header {
    display: flex;
    flex-direction: row;
    background-color: skyblue;
}

/* 뒤로가기 버튼 */
.gotoHome {
    font-size: 1.2em;
    color: white;
    cursor: pointer;
}

/* 상단배너 */
.banner {
    width: 100%;
    height: 25%;
    font-size: 24px;
}

.logo {
    width: 11em;
}

/* 로그인 인풋 박스 */
.form {
    margin-top: 2em;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.form_group {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: fit-content;
}

label {
    font-size: 20px;
}

input {
    margin-top: 6px;
    min-width: 18em;
    height: 37px;
    padding: 0px 10px;
    font-size: 16px;
    font-family: "Open Sans", sans-serif;
    background-color: #f3f3f3;
    border: 0;
    border-radius: 4px;
    margin-bottom: 31px;
    transition: all 250ms ease-in-out;
}

/* input:hover {
    background-color: #ffffff;
    box-shadow: 0px 0px 14px 0.3px #0e81ce96;
} */

input:focus {
    outline: none;
    box-shadow: 0px 0px 12px 0.8px #3474dbb2;
}

/* 버튼 박스 */
.btns {
    display: flex;
    flex-direction: row-reverse;
    width: 100%;
}

/* 로그인 버튼 */
.login_btn {
    font-size: 13px;
    padding: 5px 20px;
    margin-right: 20px;
    background-color: skyblue;
    color: #fff;
    border-radius: 3px;
    transition: all 250ms ease-in-out;
    cursor: pointer;
}

.login_btn:hover {
    background-color: snow;
    color: skyblue;
    font-weight: bolder;
}
.login_btn:focus {
    outline: none;
}

/* 회원가입 버튼 */
.register_btn {
    font-size: 13px;
    padding: 5px 20px;
    color: grey;
    border-radius: 3px;
    transition: all 250ms ease-in-out;
    cursor: pointer;
    text-decoration: underline;
}

/* SNS로그인 */
.login_sns {
    margin-top: 2rem;
    display: flex;
    flex-direction: column;
    width: 100%;
}
